<template>
    <div class="task-center-cont">
      <cust-title titleName="任务查询"></cust-title>
      <el-form ref="addForm" :inline="true" label-width="80px" size="small" label-position="left">
        <el-row>
          <el-col :span="7">
            <el-form-item label="ID:" prop="loanID">
              <el-input v-model="loanID" placeholder="请输入ID"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="客户姓名:" prop="userName">
              <el-input v-model="userName" placeholder="请输入姓名"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="呼叫号码:" prop="callPhoneNum">
              <el-input v-model="callPhoneNum" placeholder="请输入号码" :disabled="trueFlag"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <el-form :inline="true" label-width="80px" size="small" label-position="left" class="margintop30">
        <el-form-item label="生成时间:">
          <el-date-picker
            v-model="daterange"
            type="daterange"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button @click="resetData">重置</el-button>
          <el-button type="primary" class="purple-btn">查询</el-button>
        </el-form-item>
      </el-form>

      <cust-title titleName="待回复任务"></cust-title>
      <div class="wrap-table">
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            prop="taskID"
            label="任务ID"
            width="100">
          </el-table-column>
          <el-table-column
            prop="userName"
            label="客户姓名"
            width="100">
          </el-table-column>
          <el-table-column
            prop="tel"
            label="呼叫号码">
            <template slot-scope="scope">
              <span class="tel-txt">{{scope.row.tel}}</span>
              <i class="tel-icon"></i>
            </template>
          </el-table-column>
          <el-table-column
            prop="detailTime"
            label="生成时间">
          </el-table-column>
          <el-table-column
            prop="taskType"
            label="任务类型"
            width="100">
          </el-table-column>
          <el-table-column label="备注" width="110">
            <template slot-scope="scope">
              <el-button  size="small" plain icon="el-icon-edit"></el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="small" plain @click="handleChangeTask()">转接</el-button>
              <el-button size="small" type="primary" @click="handleCommitTask()" class="purple-btn">提交</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <el-dialog title="转接" :visible.sync="dialogFormVisible">
        <el-form>
          <el-form-item label="转接对象" label-width="80px">
            <el-select v-model="changeObj" placeholder="请选择转接对象">
              <el-option label="对象一" value="zhouweihua"></el-option>
              <el-option label="对象二" value="zhouweihue"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
import CustTitle from '@/components/CustTitle/CustTitle'
import CustInput from '@/components/CustInput/CustInput'

export default {
  components: { CustTitle, CustInput },
  data () {
    return {
      loanID: '',
      userName: '',
      outTime: '',
      callPhoneNum: '',
      daterange: [],
      trueFlag: true,

      tableData: [{
        taskID: '201703232',
        userName: '王小虎',
        tel: '124234324',
        detailTime: '2017/10/26 17:11:00',
        taskType: '咨询'
      }],

      dialogFormVisible: false,
      changeObj: ''
    }
  },
  methods: {
    resetData () {
      this.loanID = ''
      this.userName = ''
      this.outTime = ''
      this.callPhoneNum = ''
      this.daterange = []
    },
    handleChangeTask () {
      this.dialogFormVisible = true
    },
    handleCommitTask () {
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .task-center-cont{
    position: relative;
    height: 100%;
    padding: 0 0 40px 40px;
    overflow-x: hidden;
    overflow-y: scroll;

    .wrap-table {
      position: absolute;
      margin-top:10px;
      width:1000px;
      .tel-txt{
        margin-right: 12px;
      }
    }

    .purple-btn {
      background-color: $violetColor;
      border: none;
    }
    .margintop30 {
      margin-top: 10px;
    }
  }

</style>



